// set default due interval in milliseconds,
// days * hours * minutes * seconds * ms
const due_in_ms = (7 * 24 * 60 * 60 * 1000)
const default_dueDate = function(startDate) {
    const dueDate = (new Date(Date.now())).getTime() + due_in_ms
    return dueDate
}

export default class Task {
    constructor({id, taskName, startDate, dueDate, priority, doneDate}={}) {
        this.id = id||null
        this.taskName = taskName
        this.startDate = new Date(startDate||null)
        this.dueDate = new Date(dueDate||default_dueDate(this.startDate))
        this.priority = priority||5
        this.doneDate = doneDate||null
    }
    // this.xc will be set as database controller at runtime
    del() { this.xc.del_task(this.id) }
    write_to_DB() { this.xc.write_task(this) }
    
    setDone(doneDate) {
        this.doneDate = new Date(doneDate||Date.now())
        this.write_to_DB()
    }
}