import React, { useContext } from 'react'
import { ModalContext } from './Modal'
import TaskForm from './TaskForm'

export default function ItemInList({t, options={dateTime:DATETIME_OPTIONS.FULL}}) {
    // console.log('render item in list')
    // console.log(t)
    const modal = useContext(ModalContext)
    const handleClick = () => {
        modal.setContent(
            <TaskForm oldTask={t} handleCloseForm={()=>{modal.setContent(null)}}/>
        )
    }
    const decorate_by_priority = (p) => {
        const high = 'text-info'
        const medium = ''
        const low = 'text-body-tertiary'
        if (p >= 6) {
            return high
        }
        else if (p >= 3) {
            return medium
        }
        else {
            return low
        }
    }
    const shown_dateTime = (Date) => {
        if (options.dateTime === DATETIME_OPTIONS.FULL) {
            return Date.toLocaleString()
        }
        else if (options.dateTime === DATETIME_OPTIONS.DATE_ONLY) {
            return Date.toLocaleDateString()
        }
        if (options.dateTime === DATETIME_OPTIONS.TIME_ONLY) {
            let str = Date.toLocaleTimeString()

            // Remove penultimate 4th to 6th characters
            let slicedStr = str.slice(0, str.length - 6) + str.slice(str.length - 3)
            return slicedStr
        }
    }
    
    return (
        <div className='d-flex pb-3' onClick={handleClick}>
            <div className={ `fs-5 me-auto `+ decorate_by_priority(t.priority) }>
                {t.taskName}
            </div>
            <div className='text-body-tertiary'>
                {shown_dateTime(t.dueDate)}
            </div>
        </div>
    )
}

export const DATETIME_OPTIONS = {
    FULL: 0,
    DATE_ONLY: 1,
    TIME_ONLY: 2
}